.mw-sm {max-width: var(--bs-breakpoint-sm)!important;}
.mw-md {max-width: var(--bs-breakpoint-md)!important;}
.mw-lg {max-width: var(--bs-breakpoint-lg)!important;}
.mw-xl {max-width: var(--bs-breakpoint-xl)!important;}
.mw-xxl {max-width: var(--bs-breakpoint-xxl)!important;}

.mg {
    width: calc(100% - var(--bs-gutter-x));
}

@media only screen and (max-width: 575px) {
    .rounded-4-top-left-top-right {       
        border-top-left-radius: var(--bs-border-radius-xl)!important;
        border-top-right-radius: var(--bs-border-radius-xl)!important;
    }
    
    .rounded-4-bottom-left-bottom-right {       
        border-bottom-left-radius: var(--bs-border-radius-xl)!important;
        border-bottom-right-radius: var(--bs-border-radius-xl)!important;
    }
}

@media only screen and (min-width: 576px) {
    .rounded-sm-4-top-right-bottom-right {       
        border-top-right-radius: var(--bs-border-radius-xl)!important;
        border-bottom-right-radius: var(--bs-border-radius-xl)!important;
    }

    .rounded-sm-4-top-left-bottom-left {       
        border-top-left-radius: var(--bs-border-radius-xl)!important;
        border-bottom-left-radius: var(--bs-border-radius-xl)!important;
    }
}

.page-image {
    @media only screen and (max-width: 575px){ height: 200px!important; }
    @media only screen and (min-width: 576px){ height: 300px!important; }
}